import { Component } from '@angular/core';
import { ActionSheetController  ,AlertController, IonicPage,ModalController, NavController, NavParams, InfiniteScroll } from 'ionic-angular';
import { Auth ,NotificationProvider } from '../../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';


@IonicPage()
@Component({
  selector: 'page-unidadacademica-list',
  templateUrl: 'unidadacademica-list.html',
})
export class UnidadacademicaListPage {
  public loading: boolean = true;
  items: any=[];
  items2: any=[];
    generated = '';
    public hasFilter: boolean = false;
    idtipo: any;
    ntipo: any;
    idafiliado:any='na';
    idempresa:any;
  public gLimit: number = 50;
  public PageSegmet: string = 'foods';
  public gSearchQuery: string = '';
  username: string = "";
//private barcodeScanner:BarcodeScanner,
  constructor( public actionSheetCtrl: ActionSheetController,public alertCtrl: AlertController,public storage: Storage, public msj: NotificationProvider, public translateService: TranslateService
    ,public navCtrl: NavController,
    private _MODAL  		: ModalController, public navParams: NavParams,
     public regService: Auth
   
    ) {
      this.translateService.get('APPX').subscribe((value) => {
        this.idempresa=value
       
    })
    
  }


  
  public clearSearchg(): void {
    this.gSearchQuery = '';
  }
  showFilter()     
  {

   


  }

  
   
  pideregistros() { 
      let self = this;
      self.loading = true;
      this.regService.getReg(this.idtipo + '/todos','unidadacademicas').then((data) => {
        this.items=data;
        this.items2=data;
        self.loading = false;
  }, (err) => {
    this.msj.showError3('Error',err.error);
   // this.translateService.get('NO_OPERACION').subscribe((value) => { this.msj.showError3('Error',value);  })  
  });
  }
  ngOnInit() {
    this.storage.get('username').then((value) => {
      this.username = value;
  }); 
  }
  ionViewDidLoad() {
    this.idtipo=this.navParams.data.record._id;
    this.ntipo=this.navParams.data.record.nombre;
    this.pideregistros();
  }
  
  public loadMore(ev: InfiniteScroll) {
    this.gLimit += 50;
    setTimeout(() => ev.complete(), 1000);
  }


  deleteRecord(item : any, no) : void
  {

    let alert = this.alertCtrl.create({ title: 'Esta seguro de realizar esta operaciÃ³n?',
    buttons: [
      {text: 'Si',
        handler: () => {
              
          let recordID 		: string		= item._id;
          this.regService.deleteReg(recordID+'/'+this.username+ '/' + this.idempresa + '/' + this.idafiliado,'unidadacademicas').then((data) => {
              this.items.splice(no, 1);
              this.items = this.items.concat([]);
              this.items2=this.items
            }, (err) => {
              this.msj.showError3('Error',err.error);
          });
      
        }
      },
      {
        text: 'No',  handler: () => { }
      }
    ]
  });
  alert.present();


    
  
  }
  value:any;

  updateRecord(item : any,no) : void
   {
    let addModal = this._MODAL .create('UnidadacademicaCreatePage', { record : item,record2:{idtipo:this.idtipo,ntipo:this.ntipo} },{showBackdrop: true,enableBackdropDismiss: false});
    addModal.onDidDismiss(data => {
      if (data) {
        this.items.splice(no, 1);
        this.items = this.items.concat([data]);
        this.items2=this.items
      }
    })
    addModal.present();
    //  this.navCtrl.push('EmpresaCreatePage', { record : item });
   }
   addRecord() : void
   {
    let addModal = this._MODAL .create('UnidadacademicaCreatePage', { record : null ,record2:{idtipo:this.idtipo,ntipo:this.ntipo}},{showBackdrop: true,enableBackdropDismiss: false});
    addModal.onDidDismiss(data => {
      if (data) {
        this.items = this.items.concat([data]);
        this.items2=this.items
      }  
    })
    addModal.present();

      //this.navCtrl.push('EmpresaCreatePage');
   }
  
   conferenciaRecord2(item : any) : void
   {
    this.navCtrl.push('UnidadedificioListPage', { record : item });
   }
   conferenciaRecord3(item : any) : void
   {
    this.navCtrl.push('UnidadperiodoListPage', { record : item  ,record2:{idtipo:this.idtipo,idnombre:this.ntipo,idtipo2:item._id,idnombre2:item.nombre,idcodigo2:item.codigo}});
   }
   conferenciaRecord(item : any) : void
   {

    this.regService.getReg(item.idtipounidad+'/'+item._id,'facultadmaterias').then((data) => {
     
      let addModal = this._MODAL .create('UnidadmateriaCreatePage', { record : data,record2:{idtipo:item.idtipounidad,idtipo2:item._id}},{showBackdrop: true,enableBackdropDismiss: false});
      addModal.onDidDismiss(data => {
        if (data) {
        
        }  
      })
      addModal.present();
}, (err) => {
  let addModal = this._MODAL .create('UnidadmateriaCreatePage', { record : null,record2:{idtipo:item.idtipounidad,idtipo2:item._id}},{showBackdrop: true,enableBackdropDismiss: false});
  addModal.onDidDismiss(data => {
    if (data) {
    
    }  
  })
  addModal.present();
 // this.translateService.get('NO_OPERACION').subscribe((value) => { this.msj.showError3('Error',value);  })  
});


   
   
      
   }

  

}



